.registerContainer{
    height: 75vh;
    position: relative;
}
.register{
    position: absolute;
    top: 32rem;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 1;
    width: 800px;
    background: hsl(0, 0, 100);
    border-radius: 10px;
    box-shadow: 0 3px 25px hsla(0, 0, 0, 0.2);
    overflow: hidden;
    display: flex;
}
.register-classic{
    width: 100%;
    padding: 30px 50px;

    h1 {
        font-size: 30px;
        margin-top: 10px;
        margin-bottom: 40px;
        text-shadow: 0 2px 3px hsla(0, 0, 0, 0.1);
    }

    fieldset::after {
        content: '\f007';
        font-family: FontAwesome;
        position: absolute;
        right: 15px;
        top: 17px;
        z-index: 2;
        width: 20px;
        color: hsla(0, 0, 0, .2);
        text-align: center;
    }
  
    fieldset.text::after {
        content: '\e1bf';
    }
  
    fieldset.email::after {
        content: '\f0e0';
    }

    fieldset.password::after {
        content: '\f023';
    }

    fieldset.file::after {
        content: '\f03e';
    }

}